// Display the website title
const Title = ({ screenSize, isDarkMode }) => {
  const fontStyle = isDarkMode ? "title-font-dark" : "title-font-light";

  return (
    <>
      {screenSize === "small" ? (
        <div className={`header header-small ${fontStyle}`}>Pokmon Lookup</div>
      ) : screenSize === "medium" ? (
        <div className={`header header-med ${fontStyle}`}>Pokmon Lookup</div>
      ) : screenSize === "large" ? (
        <div className={`header header-large ${fontStyle}`}>Pokmon Lookup</div>
      ) : (
        <div className={`header header-x-large ${fontStyle}`}>
          Pokmon Lookup
        </div>
      )}
    </>
  );
};

export default Title;
